/* Container for the whole page */
.anniversaries-container {
    padding: 20px;
    padding-top: 60px;
    max-width: 1200px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
}

/* Title styling */
.anniversaries-container h2 {
    text-align: center;
    margin-bottom: 30px;
    width: 100%;
}

/* Grid container */
.anniversaries-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    gap: 25px;
    width: 100%;
    padding: 20px 0;
}

/* Individual anniversary item */
.anniversary-item {
    background-color: #f4f4f4;
    margin: 10px;
    border-radius: 10px;
    padding: 10px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    transition: transform 0.2s ease, box-shadow 0.2s ease;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 20px;
}

/* Hover effect for anniversary item */
.anniversary-item:hover {
    transform: translateY(-5px);
    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2);
}

/* Anniversary title */
.anniversary-item h3 {
    font-size: 1.2em;
    margin-bottom: 15px;
    color: #333;
}

/* Anniversary date */
.anniversary-item p {
    font-size: 1em;
    color: #555;
    margin-bottom: 10px;
}

/* Error message styling */
.error {
    color: red;
    text-align: center;
    margin-top: 20px;
    width: 100%;
}

/* Create Anniversary Form */
.create-anniversary-form {
    width: 100%;
    max-width: 600px;
    margin: 0 auto 20px;
    padding: 20px;
}

/* Form inputs */
.create-anniversary-form input,
.create-anniversary-form textarea {
    width: 100%;
    margin-bottom: 15px;
    padding: 8px;
    border: 1px solid #ddd;
    border-radius: 4px;
}

/* Button styles */
.create-anniversary-btn,
.create-anniversary-form button {
    border-radius: 4px;
    padding: 10px 24px;
    font-size: 1rem;
    cursor: pointer;
    transition: background-color 0.3s ease, transform 0.2s ease;
    border: none;
}

/* Create Button Container */
.create-btn-container {
    width: 100%;
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
}

/* Create Button */
.create-anniversary-btn {
    background-color: #007bff;
    color: white;
}

.create-anniversary-btn:hover {
    background-color: #0056b3;
    transform: scale(1.05);
}

/* Submit Button */
.create-anniversary-form button {
    background-color: #28a745;
    color: white;
    width: 100%;
}

.create-anniversary-form button:hover {
    background-color: #218838;
    transform: scale(1.05);
}

/* Delete Button */
.delete-anniversary-btn {
    background-color: #ff4d4d;
    color: white;
    padding: 6px 12px;
    border: none;
    border-radius: 4px;
    font-size: 0.8rem;
    cursor: pointer;
    transition: background-color 0.3s ease, transform 0.2s ease;
    margin-left: auto;
}

/* Hide delete buttons when the create form is open */
.anniversaries-container.show-form .delete-anniversary-btn {
    display: none;
}

/* Delete Button Hover Effect */
.delete-anniversary-btn:hover {
    background-color: #e60000;
    transform: scale(1.05);
}

/* Anniversary content */
.anniversary-item .content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 20px;
    padding-right: 10px;
}

/* Logout Button */
.logout-btn {
    position: fixed;
    top: 20px;
    right: 20px;
    background-color: #ff4d4d;
    color: white;
    border: none;
    border-radius: 4px;
    padding: 8px 16px;
    cursor: pointer;
    transition: background-color 0.3s ease, transform 0.2s ease;
    z-index: 1000;
}

.logout-btn:hover {
    background-color: #e60000;
    transform: scale(1.05);
}

/* Media Queries */
/* Tablets and smaller desktops */
@media (max-width: 1024px) {
    .anniversaries-grid {
        grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    }
}

/* Mobile devices */
@media (max-width: 768px) {
    .anniversaries-container {
        padding: 10px;
        padding-top: 50px;
    }

    .anniversaries-grid {
        grid-template-columns: 1fr;
        padding: 10px 0;
        gap: 25px;
    }

    .anniversary-item {
        padding: 20px;
    }

    .create-anniversary-form {
        padding: 10px;
    }

    .logout-btn {
        top: 10px;
        right: 10px;
        padding: 6px 12px;
        font-size: 0.9rem;
        position: absolute;
    }

    .create-anniversary-btn,
    .create-anniversary-form button {
        padding: 8px 16px;
        font-size: 0.9rem;
    }
}

/* Small mobile devices */
@media (max-width: 480px) {
    .anniversaries-container {
        padding: 8px;
        padding-top: 45px;
    }

    .anniversary-item {
        padding: 15px;
    }

    .anniversary-item h3 {
        font-size: 1.1em;
    }

    .anniversary-item p {
        font-size: 0.9em;
    }

    .create-anniversary-form input,
    .create-anniversary-form textarea {
        padding: 6px;
        margin-bottom: 10px;
    }
}

/* Print styles */
@media print {
    .logout-btn,
    .create-anniversary-btn,
    .delete-anniversary-btn {
        display: none;
    }

    .anniversary-item {
        break-inside: avoid;
        page-break-inside: avoid;
        box-shadow: none;
    }

    .anniversaries-container {
        padding: 0;
    }
}